import { getRecommendedSongList } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 推荐歌单
      recommendedSongList: [],
      // 渲染总数
      total: 0,
      // 每次插入渲染的 DOM 数量 2 行(10个)
      once: 10,
      // 当前已经渲染 DOM 的总数，从 30 开始，因为初始渲染 30 条
      countOfRender: 30,
      // 请求回来的原数据
      resData: []
    };
  },

  // 接收父组件传过来的值
  props: ['scrollTop'],

  async mounted() {
    // 获取推荐歌单(最多 100)
    const res = await getRecommendedSongList(100); // console.log('获取推荐歌单', res);
    // this.recommendedSongList = res.data.result
    // 把请求回来的原数据放到存放原数据的暂存数组中(该数组不是渲染数据的数组)

    this.resData = res.data.result; // console.log('推荐歌单', this.resData);
    // 渲染总数

    this.total = this.resData.length; // 刚开始只渲染 30 条数据

    this.recommendedSongList = this.resData.slice(0, 30); // 重置当前已经渲染 DOM 的总数

    this.countOfRender = 30; // 传给父组件加载完成标志

    if (this.recommendedSongList.length > 0) this.$emit('isLoadingOk', 1);
  },

  methods: {
    // 推荐歌单显示播放键
    playKeyMouseenter(index) {
      this.$refs.playKey[index].classList = 'playKey';
    },

    // 推荐歌单隐藏播放键
    playKeyMouseleave(index) {
      this.$refs.playKey[index].classList = 'playKey1';
    },

    // 去往歌单详情页
    goDetail(id) {
      // 去往歌单详情页
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 每次只渲染指定条数据(每次只加指定条数据到渲染数组中)
    async scrollLoad() {
      // console.log('已渲染总数:', this.countOfRender, '需渲染总数:', this.total);
      const temp = [];

      for (let i = 0; i < this.once; i++) {
        // 当 DOM 渲染完就退出
        if (this.countOfRender >= this.total) return; // 还没渲染完继续添加
        else {
          // 把请求回来的原数据先加到暂存数组中
          temp.push(this.resData[this.countOfRender]); // 当前已经渲染 DOM 的总数

          this.countOfRender += 1;
        }
      } // 然后把暂存数组的数据加到需要渲染的数组中，concat() 将一个数组合并到另一个数组


      this.recommendedSongList = this.recommendedSongList.concat(temp); // console.log('temp', temp);
    }

  },
  watch: {
    scrollTop() {
      // console.log('滚动位置改变为' + this.scrollTop, '触发滚动加载方法！');
      this.scrollLoad();
    }

  }
};