import { getDailyRecommendedSongList } from '@/request/api/discoverMusic';
export default {
  data() {
    return {
      // 是否获取每日推荐歌单
      isShow: 0,
      // 用户昵称
      userNickname: '',
      // 雷达歌单
      radarSongList: [],
      // 整个歌单横列当前位置
      nowLocation: 0
    };
  },

  async mounted() {
    // 先判断是否已登录(三元判断之后赋值)
    this.isShow = sessionStorage.getItem('cookie') ? 1 : 0;
    this.userNickname = sessionStorage.getItem("userInfo") ? JSON.parse(sessionStorage.getItem('userInfo')).nickname : '未登录'; // 获取每日推荐歌单

    const res = await getDailyRecommendedSongList(sessionStorage.getItem("cookie")); // console.log('每日推荐歌单(雷达歌单)', res);

    this.radarSongList = res.data.recommend; // console.log('雷达歌单', this.radarSongList);
  },

  methods: {
    // 推荐歌单显示播放键
    playKeyMouseenter(index) {
      this.$refs.playKey[index].classList = 'playKey';
    },

    // 推荐歌单隐藏播放键
    playKeyMouseleave(index) {
      this.$refs.playKey[index].classList = 'playKey1';
    },

    // 去往歌单详情页
    goDetail(id) {
      // 再去往歌单详情页
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 下一个
    nextSongList() {
      if (this.nowLocation !== -(205 + 18) * (this.radarSongList.length - 5)) {
        // 一次移动(图片宽度 + 右边距)
        this.nowLocation -= 205 + 18; // 移动整个 lists

        this.$refs.lists.style.transform = 'translateX(' + this.nowLocation + 'px)'; // console.log('当前位置', this.nowLocation);
      }
    },

    // 上一个
    previousSongList() {
      if (this.nowLocation !== 0) {
        this.nowLocation += 205 + 18;
        this.$refs.lists.style.transform = 'translateX(' + this.nowLocation + 'px)';
      }
    }

  },
  watch: {
    // 整个歌单横列当前位置
    nowLocation() {
      // 头尾不能按
      if (this.nowLocation === -(205 + 18) * (this.radarSongList.length - 5)) {
        this.$refs.buttonNext.style.backgroundColor = '#f5f5f5';
        this.$refs.iconNext.style.fill = '#a9a9a9';
      } else if (this.nowLocation === 0) {
        this.$refs.buttonPrevious.style.backgroundColor = '#f5f5f5';
        this.$refs.iconPrevious.style.fill = '#a9a9a9';
      } else {
        this.$refs.buttonNext.style.backgroundColor = '#ebebeb';
        this.$refs.iconNext.style.fill = '#3b3b3b';
        this.$refs.iconPrevious.style.fill = '#3b3b3b';
      }
    }

  }
};