// 导入子组件
import RadarSongList from '@/components/menu/DiscoverMusic/DiscoverCustomized/RadarSongList.vue';
import MusicHoliday from '@/components/menu/DiscoverMusic/DiscoverCustomized/MusicHoliday.vue';
export default {
  data() {
    return {
      // 是否显示加载中
      isLoading: 1,
      isLoadingOK: 0,
      // 传给子组件的滚动位置
      scrollTop: 0
    };
  },

  created() {
    this.listenerFunction();
  },

  beforeDestroy() {
    // 去除监听器
    document.removeEventListener("scroll", this.listenerFunction);
  },

  components: {
    RadarSongList,
    MusicHoliday
  },
  methods: {
    // 给监听器绑定滚动方法 handleScroll()
    listenerFunction(e) {
      // 因为属于在主页面使用滚动事件，故可对 document 添加监听器
      document.addEventListener('scroll', this.handleScroll, true);
    },

    // 滚动方法
    handleScroll() {
      let a = this.$refs.big_container.scrollTop; // 每滚动 500px 打印一次

      if (a % 500 === 0) this.scrollTop = a;
    },

    // 接收子组件加载完成的标志
    getIsLoadingOk(isLoadingOK) {
      // console.log('接收到 MusicHoliday 子组件传来的标志！', isLoadingOK);
      this.isLoadingOK = isLoadingOK;
    }

  },
  watch: {
    // 直接监听第二个子组件
    isLoadingOK() {
      this.isLoading = this.isLoadingOK == 1 ? 0 : 1;
    }

  }
};